/*排行榜详情*/

import axios from 'axios'
import jsonp from 'common/js/jsonp.js'
import {
  commonParams,
  options
} from './config'

export function getTopDetail(topId) {
  let url = 'https://u.y.qq.com/cgi-bin/musicu.fcg'
  let pdata = {
    '-': 'getUCGI6876926254623921',
    loginUin: 0,
    hostUin: 0,
    platform: 'yqq.json',
    needNewCode: 0,
    data: {
      "detail": {
        "module": "musicToplist.ToplistInfoServer",
        "method": "GetDetail",
        "param": {
          "topId": topId,
          "offset": 0,
          "num": 10,
          "period": "2019_12"
        }
      },
      "comm": {
        "ct": 24,
        "cv": 0
      }
    }
  }

  pdata = Object.assign(pdata, commonParams)

  if (process.env.NODE_ENV == 'development') {
    url = '/api/getTopDetail'

    return axios.get(url, {
      params: pdata
    }).then((res) => {
      return Promise.resolve(res.data)
    }).catch((err) => {
      console.log(err);
    })

  } else {

    return jsonp(url, pdata, options);
  }

}



